<template>
        <h1>Ingrese dos números y la operación</h1>
    <div class="container mt-5">
        <div class="row">
            <input type="text" class="form-control" v-model="num1">
            <select class="form-select form-select-sm" v-model="operacion">
                <option disabled value=''>operar</option>
                <option>+</option>
                <option>-</option>
                <option>*</option>
                <option>/</option>
            </select>
            <input type="text" class="form-control" v-model="num2">
            <button class="btn btn-outline-secondary" @click='calcular()'>Calcular</button>
        </div>

        <div>
            {{$data}}
        </div>

        <div>
            <h2>Resultado: {{num1}} {{operacion}} {{num2}} {{resul}}</h2>
        </div>
    </div>
</template>

<script>
export default {
    name:'comp1',
    data(){
        return {
            num1: '',
            num2: '',
            operacion: '',
            resul: ''
        }
    },
    computed:{
        suma(){
            return parseInt(this.num1) + parseInt(this.num2)
        },
        resta(){
            return parseInt(this.num1) - parseInt(this.num2)
        },
        multiplicacion(){
            return parseInt(this.num1) * parseInt(this.num2)
        },
        division(){
            return parseInt(this.num1) / parseInt(this.num2)
        }
    },
    methods:{
        calcular(){
            var cal;
            switch(this.operacion){
                case '+':
                    cal = this.suma;
                break;
                case '-':
                    cal = this.resta;
                break;
                case '*':
                    cal = this.multiplicacion;
                break;
                case '/':
                    cal = this.division;
                break;
                default: return;
            }
            this.resul = '='+cal
            console.log('resul',this.resul)
        }
    }
}
</script>

<style scoped>
    input{
        width: 100px;
    }
    select{
        width: 100px;
    }
    button{
        width: 80px;
    }
</style>